*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  margin: 0 1.25rem 0 1.25rem;
  font-family: 'Montserrat', sans-serif;
}

@media screen and (min-width: 550px){
  body {
      margin: 0 3.125rem 0 3.125rem;
    }
}

@media screen and (min-width: 992px) {
  body{
    max-width: 1440px;
    margin: auto;
    padding: 0 3.125rem;
  }
} 